@import url("../../components/_reset.scss");
.frontpageStandoutmedia {
    display: flex;
    // grid-template-rows: 26.26% 25.95% 17.44% 24.07% 6.19%;
    flex-direction: column;
    height: 100vh; 
    // grid-template-columns: 50% 50%;
    &header {
        height: 26.26%;
        display: grid;
        grid-template-columns: 31.59% 68.41%;
        grid-template-rows: 30% 70% ;
        }
    }
        .frontpageStandoutmediaheader__Logo {
            grid-template-columns: 1/2;
            grid-template-rows: 1/2;
            display: flex;
            align-items: center;
            justify-content: center;
        
            }
            .frontpageStandoutmediaheader__Logoimg {
                width: 50%;
            }
            .frontpageStandoutmediaheader__Text {
                grid-column: 2/3;
                grid-row: 1/2;
                &socials {
               height: 50%;
               width: 80%;
               grid-column: 2/3;
             grid-row: 1/2;
             display: flex;
             justify-content: space-around;
             padding-left: 10%;
             align-items: center;
             &Links {
                text-decoration: none;
                font-size: .5rem;
                font-family: Arial, Helvetica, sans-serif;
             }
            }

         }
                .frontpageStandoutmediaheader__Textmenu {
                    grid-column: 2/3;
                    height: 50%;
                    width: 80%;
                    display: flex;
                    align-items: center;
                    justify-content: space-evenly;
                    &Links {
                        font-size: .7rem;
                        text-decoration: none;
                        color: #fff;
                    }
                 }
                 .frontpageStandoutmediaheader__Hero {
                    grid-column: 1/3;
                    grid-row: 2/3;
                    background-size: cover;
                    
                    display: flex;
                    flex-direction: column;
                    align-items: flex-start;
                    justify-content: center;
                    border: 2px solid black;
                    padding-left: 10%;
                    &headline {
                        color: #fff;
                        font-size: 2em;
                    &Button {
                        width: 10%;
                        height: 20%;
                        color: #fff;
                        background-color: red;
                        &Txt {
                            color: white;
                        }
                    }
                 }
                }
                 .frontpageStandoutmediamain {
                    grid-column: 1/3;
                    display: flex;
                    flex-direction: column;
                    height: 25.95%;
                    &welcome {
                        padding-block: .5%;
                        height: 50%;
                        display: flex;
                        flex-direction: column;
                        justify-content: center;
                        &__Headline {
                            font-size: .7em;

                        }
                        .frontpageStandoutmediamainwelcome__Text {
                            font-size: .5rem;
                        }
                    }
                }
                        .frontpageStandoutmediamainwelcome__Text {
                            font-size: 10px;
                            padding-top: .3%;


                        }
                        .frontpageStandoutmediamain__Img {
                            display: flex;
                            justify-content: space-around;
                            align-items: center;
                            padding-inline: 10%;
                            grid-column: 1/3;
                            &box {
                                display: flex;
                                flex-direction: column;
                                align-items: center;
                                &Img {
                                    height: 80%;
                                    width: 3em;
                                    &text {
                                        text-align: center;
                                        font-size: 10px;
                                    }
                                }
                            }
                            }
                                    .frontpageStandoutmediamain2 {
                                        border: 5px solid black;
                                        display: flex;
                                        &__text {
                                            width: 50%;
                                            height: 50%;
                                            border: 2px solid red;
                                            &Headline {
                                                font-size: .7em;

                                            }
                                        }
                                    }
                                            .frontpageStandoutmediamain2__textBread {
                                                font-size: 4px;
                                            }
                                            
                                        

                                    
                                
                                     
                                


                        


                 
            
            

            







